import styled from "styled-components"

export const Wrapper = styled.div`
    margin: var(--su) 0;
    padding: var(--su) 0;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }

    @media (min-width: 800px) {
        background-color: inherit; // Requires parent with a background color
        color: inherit;
        margin: 0;
        position: relative;
        z-index: 1;
    }

`