import styled from "styled-components"

export const Wrapper = styled.div`

    a {
        background: none;
        border: var(--line-width) solid ${props => props.theme.button.border.normal};
        box-shadow: 2px 2px 0 ${props => props.theme.button.border.normal};
        color: ${props => props.theme.button.text.normal};
        display: inline-block;
        line-height: var(--line-height-tight);
        margin-bottom: 4px;
        padding: 12px 32px;
        position: relative;
        text-decoration: none;
        transition: 0.1s linear box-shadow;
        user-select: none;

        &:hover { 
            border-color: ${props => props.theme.button.border.hover};
            box-shadow: 4px 4px 0 ${props => props.theme.button.border.hover};
            color: ${props => props.theme.button.text.hover};
        }
    
    }

`