import { motion } from "framer-motion"
import styled from "styled-components"

export const ImageWrapper = styled(motion.div)`
    background-color: ${props => props.theme.background.normal};
    position: relative;
    user-select: none;
    z-index: 2;

    > div {
        height: calc(100vw * .56);
    }

    @media (min-width: 800px) {

        > div {
            height: 100vh;
            left: 50%;
            min-height: 1280px;
            position: absolute;
            transform: translateX(-50%);
        }

    }

`

export const ImageCell = styled.div`
    grid-row: 1;
    position: relative;
`

export const ContentCell = styled.div`
    grid-row: 1;
    margin-top: var(--su);

    @media (min-width: 800px) {
        margin-top: 0;
    }

`

export const Wrapper = styled.div`
    background-color: transparent;
    margin-bottom: calc(var(--su) * -2);

    @media (min-width: 800px) {
        margin-bottom: 0;
    
        &:nth-child(odd) {

            ${ImageCell} {
                grid-column: 2;
                margin-left: calc(var(--su) * -1);
            }

            ${ImageWrapper} {

                @media (min-width: 800px) {
                    margin-right: auto;
                }    

            }

            ${ContentCell} {
                grid-column: 1;
            }

        }

        &:nth-child(even) {

            ${ImageCell} {
                grid-column: 1;
                margin-right: calc(var(--su) * -1);
            }

            ${ImageWrapper} {

                @media (min-width: 800px) {
                    margin-left: auto;
                }    
                
            }

            ${ContentCell} {
                grid-column: 2;
            }

        }

    }

`
